import {
  Grid,
  Table,
  TableBody,
  TableCell,
  TableHead,
  IconButton,
  TableRow,
  Tooltip,
  Typography,
} from "@material-ui/core";
import { setSnackBar } from "../redux/actions";
import DeleteIcon from "@material-ui/icons/Delete";
import React, { useEffect } from "react";
import VisibilityIcon from "@material-ui/icons/Visibility";
import { createStore } from "polotno/model/store";
import { Workspace } from "polotno/canvas/workspace";
import { ZoomButtons } from "polotno/toolbar/zoom-buttons";
import TemplateForm from "./TemplateForm";
import { connect } from "react-redux";
import GetAppIcon from "@material-ui/icons/GetApp";
let store = createStore({
  key: "nFA5H9elEytDyPyvKL7T",
});
store.loadJSON({
  width: 300,
  height: 300,
  pages: [
    {
      id: "vktP69-zv7",
      background: "white",
    },
  ],
});
function Dashboard(props: any) {
  const [list, setList] = React.useState<any>([]);
  const [sizeConfiguration, setSizeConfiguration] = React.useState<any>({
    rows: 10,
    columns: 10,
    height: 100,
    width: 100,
    format: "sheets",
    shape: "square",
  });
  const [loading, setLoading] = React.useState<boolean>(false);
  const [index, setIndex] = React.useState<any>();
  const intialLoad = () => {
    setLoading(true);
    fetch("/api/template/all")
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        setLoading(false);
        setList(data);
      })
      .catch(() => {
        setLoading(false);
        props.setSnackBar({
          snackbar: {
            message: "Something went wrong!!",
            severity: "warning",
            open: true,
          },
        });
      });
  };
  useEffect(() => {
    intialLoad();
  }, []);

  const load = (row: any) => {
    store.loadJSON(JSON.parse(row.json));
    setIndex(row.id);
  };
  const download = async (row: any) => {
    setLoading(true);
    fetch("/api/template/download", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        image: store.toDataURL(),
        template: sizeConfiguration,
      }),
    })
      .then((response) => {
        if (response.ok) {
          return response.blob();
        } else {
          throw new Error();
        }
      })
      .then((blob) => {
        setLoading(false);
        var url = window.URL.createObjectURL(blob);
        var a = document.createElement("a");
        a.href = url;
        a.download = "Download.pdf";
        document.body.appendChild(a); // we need to append the element to the dom -> otherwise it will not work in firefox
        a.click();
        a.remove(); //afterwards we remove the element again
      })
      .catch(() => {
        setLoading(false);
        props.setSnackBar({
          snackbar: {
            message: "Something went wrong!!",
            severity: "warning",
            open: true,
          },
        });
      });
  };
  const deleteTemplate = async (row: any) => {
    setLoading(true);
    const id = row.id;
    fetch("/api/template/" + id, {
      method: "DELETE",
    })
      .then(() => {
        props.setSnackBar({
          snackbar: {
            message: "Deleted Successfully",
            open: true,
          },
        });
        intialLoad();
      })
      .catch(() => {
        setLoading(false);
        props.setSnackBar({
          snackbar: {
            message: "Something went wrong!!",
            severity: "warning",
            open: true,
          },
        });
      });
  };
  const setConfigurationValues = (values: any) => {
    console.log(values);
    setSizeConfiguration(values);
  };
  return (
    <>
      {list && list.length > 0 ? (
        <div className="Templates">
          <Grid container>
            <TemplateForm
              values={sizeConfiguration}
              setConfigurationValues={setConfigurationValues}
            ></TemplateForm>
          </Grid>
          <Grid container>
            <Grid item>
              <Table aria-label="simple table" size="small">
                <TableHead>
                  <TableRow style={{ fontWeight: "bold" }}>
                    <TableCell>Template Name</TableCell>
                    <TableCell></TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {list?.map((row: any) => (
                    <TableRow key={row.id}>
                      <TableCell component="th" scope="row">
                        {row.name}
                      </TableCell>
                      <TableCell align="right">
                        <Tooltip title="Preview">
                          <IconButton
                            aria-label="preview"
                            size="medium"
                            disabled={loading}
                            style={{ marginLeft: "5px" }}
                            onClick={() => {
                              load(row);
                            }}
                          >
                            <VisibilityIcon fontSize="inherit" />
                          </IconButton>
                        </Tooltip>
                        <Tooltip title="Download">
                          <IconButton
                            aria-label="download"
                            size="medium"
                            disabled={loading || index !== row.id}
                            style={{ marginLeft: "5px" }}
                            onClick={() => {
                              download(row);
                            }}
                          >
                            <GetAppIcon fontSize="inherit" />
                          </IconButton>
                        </Tooltip>
                        {props.admin && (
                          <Tooltip title="Delete">
                            <IconButton
                              aria-label="delete"
                              size="medium"
                              disabled={loading}
                              style={{ marginLeft: "5px" }}
                              onClick={() => {
                                deleteTemplate(row);
                              }}
                            >
                              <DeleteIcon fontSize="inherit" />
                            </IconButton>
                          </Tooltip>
                        )}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Grid>{" "}
            <Grid item>
              <div
                style={{
                  width: "400px",
                  height: "400px",
                }}
              >
                <Workspace store={store} />
                <ZoomButtons store={store} />
              </div>
            </Grid>
          </Grid>
        </div>
      ) : (
        <Typography variant="h4">
          Please start by creating new Templates in Editor section
        </Typography>
      )}
    </>
  );
}

const mapStateToProps: any = (state: any) => {
  console.log(state.admin?.admin);
  return { admin: state.admin?.admin, ...state.snackbar };
};
export const DashboardComponent = connect(mapStateToProps, { setSnackBar })(
  Dashboard
);
