import { Button, MenuItem, Select } from "@material-ui/core";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import CircularProgress from "@material-ui/core/CircularProgress";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import clsx from "clsx";
import React from "react";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexWrap: "wrap",
    },
    margin: {
      margin: theme.spacing(1),
    },
    withoutLabel: {
      marginTop: theme.spacing(3),
    },
    textField: {
      width: "25ch",
    },
    selectField: {
      width: "200px",
    },
  })
);

export default function TemplateForm(props: any) {
  const classes = useStyles();
  const [values, setValues] = React.useState<any>({
    rows: 10,
    columns: 10,
    height: 100,
    width: 100,
    name: "New Template",
    // imageUrl: "",
    format: "sheets",
    shape: "square",
  });

  const handleChange = (prop: any) => (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    const newValue = { ...values, [prop]: event.target.value };
    setValues(newValue);
    if (props.setConfigurationValues) {
      props.setConfigurationValues(newValue);
    }
  };
  const handleDropdownChange = (target: any, value: any) => {
    const newValues = { ...values };
    newValues[target] = value;
    setValues(newValues);
    props.setConfigurationValues(newValues);
  };
  const saveTemplate = () => {
    props.saveTemplate(values);
  };
  return (
    <div className={classes.root}>
      {props.saveTemplate ? (
        <>
          <FormControl
            size="small"
            className={clsx(classes.margin, classes.textField)}
            variant="outlined"
          >
            <InputLabel htmlFor="outlined-adornment-template-name">
              Template Name
            </InputLabel>
            <OutlinedInput
              id="outlined-adornment-template-name"
              type="text"
              value={values.name}
              onChange={handleChange("name")}
              label="Template Name"
            />
          </FormControl>
        </>
      ) : (
        <>
          <FormControl
            variant="outlined"
            size="small"
            className={clsx(classes.margin, classes.textField)}
          >
            <InputLabel id="demo-simple-select-outlined-label">
              Format
            </InputLabel>
            <Select
              style={{ minWidth: "120px" }}
              labelId="demo-simple-select-outlined-label"
              id="demo-simple-select-outlined"
              value={props.values.format}
              onChange={(event: any) => {
                handleDropdownChange("format", event?.target.value);
              }}
              label="Format"
            >
              <MenuItem value={"sheets"}>Sheets</MenuItem>
              <MenuItem value={"rolls"}>Rolls</MenuItem>
            </Select>
          </FormControl>
          <FormControl
            size="small"
            className={clsx(classes.margin, classes.textField)}
            variant="outlined"
          >
            <InputLabel htmlFor="outlined-adornment-rows">Rows</InputLabel>
            <OutlinedInput
              id="outlined-adornment-rows"
              type="text"
              value={values.rows}
              onChange={handleChange("rows")}
              labelWidth={70}
            />
          </FormControl>
          <FormControl
            size="small"
            className={clsx(classes.margin, classes.textField)}
            variant="outlined"
          >
            <InputLabel htmlFor="outlined-adornment-columns">
              Columns
            </InputLabel>
            <OutlinedInput
              id="outlined-adornment-columns"
              type="text"
              value={values.columns}
              onChange={handleChange("columns")}
              labelWidth={70}
            />
          </FormControl>
          <FormControl
            size="small"
            className={clsx(classes.margin, classes.textField)}
            variant="outlined"
          >
            <InputLabel htmlFor="outlined-adornment-height">Height</InputLabel>
            <OutlinedInput
              id="outlined-adornment-height"
              type="text"
              value={values.height}
              onChange={handleChange("height")}
              labelWidth={70}
            />
          </FormControl>
          <FormControl
            size="small"
            className={clsx(classes.margin, classes.textField)}
            variant="outlined"
          >
            <InputLabel htmlFor="outlined-adornment-width">Width</InputLabel>
            <OutlinedInput
              id="outlined-adornment-width"
              type="text"
              value={values.width}
              onChange={handleChange("width")}
              labelWidth={70}
            />
          </FormControl>
        </>
      )}{" "}
      <FormControl
        size="small"
        className={clsx(classes.margin)}
        variant="outlined"
      >
        {props.saveTemplate && (
          <Button
            variant="contained"
            color="primary"
            onClick={saveTemplate}
            disabled={props.loading}
          >
            Save Template
            {props.loading && (
              <CircularProgress
                color="secondary"
                style={{ marginLeft: "5px", width: "25px", height: "25px" }}
              />
            )}
          </Button>
        )}
      </FormControl>
    </div>
  );
}
