export const CustomOutlinesPanel = observer((store: any) => {
  const [outlines, setOutlines] = React.useState<any>([]);
  async function loadOutlines() {
    const outlines: any = [];
    setOutlines(outlines);
    // wait to emulate network request
    // for demo images are hard coded
    // in real app here will be something like JSON structure
    setOutlines([
      {
        url: "/outlines/1.png",
      },
      {
        url: "/outlines/2.png",
      },
      {
        url: "/outlines/3.png",
      },
      {
        url: "/outlines/4.png",
      },
      {
        url: "/outlines/5.png",
      },
      {
        url: "/outlines/6.png",
      },
      {
        url: "/outlines/7.png",
      },
      {
        url: "/outlines/8.png",
      },
      {
        url: "/outlines/9.png",
      },
    ]);
  }

  React.useEffect(() => {
    loadOutlines();
  }, []);
  console.log(store);
  return (
    <div style={{ height: "100%", display: "flex", flexDirection: "column" }}>
      <ImagesGrid
        images={outlines}
        getPreview={(image: any) => image.url}
        onSelect={async (image: any, dimension: any) => {
          const { width, height } = await getImageSize(image.url);
          store.store.activePage.addElement({
            type: "image",
            src: image.url,
            width,
            height,
            x: dimension ? dimension.x : 50,
            y: dimension ? dimension.y : 50,
          });
        }}
        rowsNumber={2}
        isLoading={!outlines.length}
        loadMore={false}
      />
    </div>
  );
});

